.z-login{
    flex: 1 1;
    color: #b3b3b3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center
}
.z-login nav{

    display: flex;
    flex-direction: column;
    margin-top: 2vh;
    justify-content: center;
    align-items: center
}

.z-login nav h2{
    font-size: 4vh;
}
.z-login input{
    border: none;
    outline: none;
    width: 40vh;
    height: 100%;
    margin-left: 4vw;
    color: #b3b3b3;
}
input:-webkit-autofill { box-shadow: 0 0 0px 1000px white inset !important;}
.z-login .zhang{
    width: 90%;
    height: 5vh;
    border-bottom: 1px solid #ccc;
    padding: 2vh 0
}


.z-login .mima{
    width: 90%;
    height: 5vh;
    border-bottom: 1px solid #ccc;
    padding: 2vh 0;
    font-size: 2vh;
}
.z-login span{
    padding: 0 0 0 2vw;
    color: #5a5a5a;
    font-size: 2vh;
}
.z-login .btn{
    display: flex;
    justify-content: space-around;
    width: 90%;
    margin-top: 7vh;
}

.z-login .btn button{
    width: 40vw;
    height: 6vh;
    border-radius: 5px;
    outline: none;
    font-size: 2vh;
}
.z-login .btn button:first-child{
    background: white;
    border: 1px solid #ccc
