.Cart{
    height: 100vh;
    display: flex;
    flex-direction: column;
    font-family: '\5FAE\8F6F\96C5\9ED1';
}

.c-header{
    height: 9vh;
    border-bottom: 1px solid #ccc;
    display: flex;
    justify-content: center;
    align-items: center;
    line-height: 9vh;
    position: relative;
}

.c-header b{
    font-size: 3vh;
    font-style: normal;
    font-weight: 100
}
.c-header span{
    position: absolute;
    left: 3vh;
    font-size: 4vh;
}
.c-header em{
    position: absolute;
    right: 3vh;
    font-size: 3vh;
    font-style: normal;
}

.c-section{
    flex: 1 1;
    flex-direction: column;
    overflow: auto;
    display: flex;
    align-items: center;

   
}
.c-section ul{
    width: 90%;
    margin-top: 1vh;
}
.c-section ul li{
    display: flex;
    justify-content: space-between;
    margin-top: 1vh;
    
}
.c-section .left{
    width: 5vw;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.c-section .left input{
    width: 5vw;
    height: 3vh;
    border-radius: 50%;
    
}

.c-section .right{
    flex: 1 1;
    margin-left: 3vw;
    display: flex;
}
.c-section .right .img{
    width: 30vw;
    height: 17vh;
    background: #ccc;
}

.c-section .right .xinxi{
    flex: 1 1;
    margin-left: 2vh; 
    flex-direction: column; 
    justify-content: space-between;
    display: flex;
    padding: 1vh;
    
