    margin-top: 1vh;
}
.c-section .right .sum{
    display: flex;
    justify-content: space-between;
    
}

.c-section .right nav{
    display: flex;
    border: 1px solid #ccc;
    
}
.c-section .right nav input{
    width: 8vw;
    height: 3vh;
    outline: none;
    border: 0;
